/* eslint-disable jsx-a11y/anchor-is-valid */
import "./header.css";
import logo from "../../assets/images/logo.png";
import NavItem from "../../components/nav-items/nav-items";
import profile_header from "../../assets/images/profile-header.jpg";

const header = () => {
  return (
    <div className="navbar navbar-expand-md navbar-dark main-navbar">
      <div className="container">
        <a href="/#" className="navbar-brand">
          <img className="header-logo" src={logo} alt="" />
        </a>
        <button
          className="navbar-toggler nav-toggle"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#main_menu"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="main_menu">
          <ul className="navbar-nav ms-auto">
            <NavItem>
              <a href="/#" className="nav-link active-class">
                Home
              </a>
            </NavItem>
            <NavItem>
              <a href="/#" className="nav-link">
                Browse
              </a>
            </NavItem>
            <NavItem>
              <a href="/#" className="nav-link">
                Details
              </a>
            </NavItem>
            <NavItem>
              <a href="/#" className="nav-link">
                Streams
              </a>
            </NavItem>
            <NavItem>
              <div className="profile-header">
                <a href="/#" className="nav-link">
                  Profile
                </a>
                <img className="profile-img" src={profile_header} alt="" />
              </div>
            </NavItem>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default header;
