import "./most-popular.css";
import { Card, SectionTitle, SectionWrapper } from "../../components/index";
import mostPopularData from "../../data/most-popular-data";

const mostPopular = () => {
  const cards = mostPopularData.map((card) => {
    return (
      <Card
        key={card.id}
        image={card.image}
        title={card.title}
        category={card.category}
        rate={card.rate}
        download={card.download}
      />
    );
  });
  return (
    <>
      <SectionWrapper>
        <SectionTitle>
          <span className="underline">Most Popular</span> <span>Right Now</span>
        </SectionTitle>
        <div className="most-popular-items">{cards}</div>
      </SectionWrapper>
    </>
  );
};

export default mostPopular;
