import "./buttons.css";

const primaryButton = (props) => {
  return (
    <div className="button primary-btn">
      <a href="/#">{props.children}</a>
    </div>
  );
};

const secondaryButton = (props) => {
  return (
    <div className="button secondary-btn">
      <a href="/#">{props.children}</a>
    </div>
  );
};

export default primaryButton;
export { secondaryButton };
