import "./gaming-library.css";
import {
  GamingCard,
  SectionTitle,
  SectionWrapper,
} from "../../components/index";
import gamingLibraryData from "../../data/gaming-library-data";

const gamingLibrary = () => {
  const gamingCards = gamingLibraryData.map((card) => {
    return (
      <GamingCard
        key={card.id}
        image={card.image}
        title={card.title}
        category={card.category}
        date_added={card.date_added}
        hours_played={card.hours_played}
        currently={card.currently}
      />
    );
  });
  return (
    <>
      <SectionWrapper>
        <SectionTitle>
          <span className="underline">Your Gaming</span> <span>Library</span>
        </SectionTitle>
        <div className="gaming-library-cards">{gamingCards}</div>
      </SectionWrapper>
    </>
  );
};

export default gamingLibrary;
